  // Library/Playlist Management
  const handleDeleteTrack = async (trackId, trackTitle) => {
    if (!db || !userId) {
      showMessage('Firebase not initialized or user not authenticated.', 'error');
      return;
    }
    try {
      const { doc, deleteDoc, collection, getDocs, query, where, updateDoc, arrayRemove } = await import('firebase/firestore');

      await deleteDoc(doc(db, `artifacts/${FIRESTORE_APP_ID}/users/${userId}/tracks`, trackId));

      const playlistsRef = collection(db, `artifacts/${FIRESTORE_APP_ID}/users/${userId}/playlists`);
      const playlistsSnapshot = await getDocs(playlistsRef);

      const batchUpdates = [];
      playlistsSnapshot.forEach(playlistDoc => {
          const playlistData = playlistDoc.data();
          if (playlistData.trackIds && playlistData.trackIds.includes(trackId)) {
              batchUpdates.push(updateDoc(doc(db, `artifacts/${FIRESTORE_APP_ID}/users/${userId}/playlists`, playlistDoc.id), {
                  trackIds: arrayRemove(trackId)
              }));
          }
      });
      await Promise.all(batchUpdates);

      if (currentTrack && currentTrack.id === trackId) {
          setCurrentTrack(null);
          setIsPlaying(false);
          if (audioRef.current) {
              audioRef.current.pause();
              audioRef.current.src = '';
          }
      }
      showMessage(`"${trackTitle}" deleted from your library and all playlists.`, 'success');
    } catch (error) {
      console.error("Error deleting track:", error);
      showMessage(`Failed to delete "${trackTitle}": ${error.message}`, 'error');
    }
  };

